import React from 'react';
import fiberSturcture from './captures/fiber_structure.png'
import fiberRelationship from './captures/fiber_relationship.jpeg'
import workInProgress from './captures/workInProgress.png'
export default function Post1() {
  return (
    <>
      <h1 style={{ textAlign: 'center' }}>React Fiber</h1>
      <p>大家以前是否有听过fiber这种数据结构呢？它是于React16之后引入的，引入的主要原因就是为了解决增量更新的问题（Incremental Rendering）。</p>
      <p>在React16以前，React用到的Reconciler（协调器）叫Stack Reconciler。它主要的特征是协调过程如同函数的调用堆栈一般，过程是同步的，无法中断。</p>
      <p>你是否有疑惑：无法中断就无法中断呗，有什么大问题吗？</p>
      <p>这是因为，在一个页面，浏览器只会分配一个主线程来处理包含js代码的执行、样式布局、事件响应及垃圾回收等在内的任务。如果一个任务的执行时间过长，其它任务的正常运行势必会受到影响。React的reconciliation过程并不算快，尤其当页面遇到比较大的更新时，协调会耗费比较多的时间。在这期间，用户无法与页面进行交互（因为主线程忙），整个页面卡住了，用户体验急剧下降。</p>
      <p>为了解决这个问题，React引入了一种叫作Fiber的数据结构 以及 增量更新的概念。</p>
      <p>什么是增量更新？它指的是React的协调过程变成异步的了。它可以被打断，并且在打断后还能再恢复，在原先工作的基础上继续进行（增量更新因此得名）。这样，React的协调不会阻塞浏览器对其它事件的处理，如响应用户事件、更新动画等等。在用户看来，页面并没有卡顿，还是高响应度的，用户体验得到了极大的提高！</p>
      <p>有人可能会问了：“既然增量更新这么好，为什么以前不弄呢？”这是因为之前的Stack Reconciler没有使用类似于Fiber的数据结构。它没有把大的工作（Reconciliation）拆分成以Fiber为单位的一个个小的工作单元（UnitOfWork），也没有记录当前协调所处位置的机制，因此它不可能实现增量更新。</p>
      <hr />
      <p>好了，说了这么多，进入我们今天的正题，好好看下fiber长什么样吧</p>
      <p>首先是Fiber的数据结构：</p>
      <img src={fiberSturcture} />
      <p>可以看到，fiber本质上是一个JavaScript对象，它涵盖了很多信息，包括Instance，也包括Effects。我们今天重点关注和Fiber树相关的四个属性——return、child、sibling和index</p>
      <p>在React15及之前的版本，reconciliation会生成VirtualDOM树；在16之后，除了生成VirtualDOM树，还会生成Fiber树。VirtualDOM树和Fiber树的层级关系是差不多的，但还是有一些细微差别的，不能完全等同。另外，Fiber上还携带了fiber需要完成的工作信息（如更新DOM，移除Ref，调用回调函数等，这些记录在Effects相关字段上）。因此，Fiber在React源码中又被称作UnitOfWork（工作单元）。</p>
      <p>我们继续来看Fiber的数据结构。Fiber的child指的是它的子节点，而return指的是它的父节点。如果碰到fiber有多个子节点的情况该怎么办呢？我们注意到，fiber的数据结构里并没有children，只有child。而child不能是数组，它只能指向一个特定的fiber。在这种情况下，sibling和index属性就派上用场了。假如父biber下有多个子fiber，那么父fiber会将child指向它的第一个子fiber，剩下的子fiber通过sibling属性相连——即第一个子fiber的sibling指向第二个子Fiber，第二个子Fiber的sibling指向第三个子Fiber……依此类推，最后一个子fiber的sibling值为null（代表它后面没有相邻的fiber了）。对于这些有sibling的子fiber来说，它们依靠index来判断它们在整个子级中的位置——第一个子fiber的index值为0，第二个index值为1……依此类推。</p>
      <p>讲到这里，你应该对Fiber树的结构有一个比较清晰的认识了吧，以下是我从其它地方摘来的图片供大家参考：</p>
      <img src={fiberRelationship} alt="" />
      <p>那为什么引入了Fiber后，协调的过程就是可中断再恢复的呢？首先，React的协调是有规律的，它采用的是深度优先算法，从根（HostRoot）出发，顺着child指针一路向下，找到最左边(index等于0)、最深层(child等于null)的那个fiber，计算出它待处理的Effects。然后看这个fiber有没有兄弟元素(sibling)，有的话将指针移到这个兄弟元素。再看这个兄弟元素有没有子元素，有的话我们也是顺着child指针一路向下，找到它最左边、最深层的那个子fiber，计算出该fiber待执行的Effects……一直重复这个过程。&emsp;当fiber没有兄弟元素(sibling为null)时，我们知道我们已经遍历完了这一层的所有子fiber，需要返回上一级了，这时我们就可以通过fiber的return属性找到上一级的那个fiber了。找到后首先我们计算出这个fiber所需要执行的Effects，然后把指针移向它的兄弟元素，再重复以上操作（找最左边、最深层的子Fiber）……这样一顿操作下来，我们就会回到当初的原点(HostRoot）了，整个协调过程完成。</p>
      <p>协调对象如果是一颗比较大的Fiber树的话就会比较费时，因此我们需要一种机制来记录当前工作的进度，这样我们中断后再回来还能继续在之前中断的地方工作。为此，React定义了一个模块级别的变量：workInProgress。它的取值是null或Fiber。</p>
      <img src={workInProgress} alt="" />
      <p>我们当前协调到哪个Fiber，只需要把这个Fiber的引用赋值给模块变量workInProgress就好了。这样当协调被中断、再回来继续工作时，只需要从workInProgress引用的那个Fiber出发，按深度优先算法一级级遍历即可，毋须担心工作进度的丢失，实现了协调的异步化。</p>
    </>
  )
}