import React, { useCallback, useState } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { BrowserRouter as Router, Link, Route } from 'react-router-dom';
// import Post from '../Post';
import { withRouter } from "react-router";
import IconLocation from '../../assets/images/cd-icon-location.svg';
import IconMovie from '../../assets/images/cd-icon-movie.svg';
import IconPicture from '../../assets/images/cd-icon-picture.svg';
import postMap from '../../posts'
import { CSSTransition, TransitionGroup, SwitchTransition } from 'react-transition-group';

function Timeline(props) {
  const [post, setPost] = useState(null);
  const PostComponent = postMap[post];
  const refCallback = useCallback((ref) => {
    if (ref) {
      const reminded = sessionStorage.getItem('reminded');
      if (!reminded) {
        sessionStorage.setItem('reminded', true);
        setTimeout(() => {
          ref.classList.add('jump')
        }, 400)
      }
    }
  }, [])
  return (
    <SwitchTransition>
      <CSSTransition classNames="slide" timeout={400} key={post}>
        {post === null ? <section id='cd-timeline' className='cd-container'>
          <div className='cd-timeline-block'>
            <div className='cd-timeline-img cd-picture'>
              <img src={IconPicture} alt='Picture' />
            </div>
            <div className='cd-timeline-content' onClick={() => setPost('Post1')}>
              <h2>Title of section 1</h2>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Iusto, optio, dolorum provident rerum aut hic
                quasi placeat iure tempora laudantium ipsa ad debitis unde? Iste voluptatibus minus veritatis qui ut.
              </p>
              <a href='#0' className='cd-read-more'>
                Read more
              </a>
              <span className='cd-date'>Jan 14</span>
            </div>
          </div>

          <div className='cd-timeline-block'>
            <div className='cd-timeline-img cd-movie'>
              <img src={IconMovie} alt='Movie' />
            </div>

            <div className='cd-timeline-content' onClick={() => setPost('Post2')}>
              <h2>Title of section 2</h2>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Iusto, optio, dolorum provident rerum aut hic
                quasi placeat iure tempora laudantium ipsa ad debitis unde?
              </p>
              <a href='#0' className='cd-read-more'>
                Read more
              </a>
              <span className='cd-date'>Jan 18</span>
            </div>
          </div>

          <div className='cd-timeline-block'>
            <div className='cd-timeline-img cd-picture'>
              <img src={IconPicture} alt='Picture' />
            </div>

            <div className='cd-timeline-content'>
              <h2>Title of section 3</h2>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Excepturi, obcaecati, quisquam id molestias eaque
                asperiores voluptatibus cupiditate error assumenda delectus odit similique earum voluptatem doloremque
                dolorem ipsam quae rerum quis. Odit, itaque, deserunt corporis vero ipsum nisi eius odio natus ullam
                provident pariatur temporibus quia eos repellat consequuntur perferendis enim amet quae quasi repudiandae
                sed quod veniam dolore possimus rem voluptatum eveniet eligendi quis fugiat aliquam sunt similique aut
                adipisci.
              </p>
              <a href='#0' className='cd-read-more'>
                Read more
              </a>
              <span className='cd-date'>Jan 24</span>
            </div>
          </div>

          <div className='cd-timeline-block'>
            <div className='cd-timeline-img cd-location'>
              <img src={IconLocation} alt='Location' />
            </div>

            <div className='cd-timeline-content'>
              <h2>Title of section 4</h2>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Iusto, optio, dolorum provident rerum aut hic
                quasi placeat iure tempora laudantium ipsa ad debitis unde? Iste voluptatibus minus veritatis qui ut.
              </p>
              <a href='#0' className='cd-read-more'>
                Read more
              </a>
              <span className='cd-date'>Feb 14</span>
            </div>
          </div>

          <div className='cd-timeline-block'>
            <div className='cd-timeline-img cd-location'>
              <img src={IconLocation} alt='Location' />
            </div>

            <div className='cd-timeline-content'>
              <h2>Title of section 5</h2>
              <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Iusto, optio, dolorum provident rerum.</p>
              <a href='#0' className='cd-read-more'>
                Read more
              </a>
              <span className='cd-date'>Feb 18</span>
            </div>
          </div>

          <div className='cd-timeline-block'>
            <div className='cd-timeline-img cd-movie'>
              <img src={IconMovie} alt='Movie' />
            </div>

            <div className='cd-timeline-content'>
              <h2>Final Section</h2>
              <p>This is the content of the last section</p>
              <span className='cd-date'>Feb 26</span>
            </div>
          </div>
        </section> : <div>
          <div id="post_container">
            <div className="window_header">
              {/* <div class="circle" /> */}
              <span className="circle circle1" ref={refCallback} title="返回" onClick={() => setPost(null)} />
              <span className="circle circle2" />
              <span className="circle circle3" />
            </div>
            <div className="post_content">
              <PostComponent />
            </div>
          </div>
          <div style={{ height: '130px' }}></div>
        </div>}
      </CSSTransition>
    </SwitchTransition>
  );
}

export default withRouter(Timeline)